<template>
  <div class="main">
    <div class="row">
      <div class="container coursePageAll">
        <div class="coursePageTitle">
          <h3>教師專區</h3>
        </div>
        <Header />
        <div class="tableStyle1">
          <table>
            <tr>
              <th>建立日期</th>
              <th>功能</th>
              <th>班級名稱</th>
              <th>課程代碼</th>
              <th>課程主題</th>
              <th>狀態</th>
              <th>課程內容</th>
            </tr>
            <tr>
              <td
                class="tableNoContent"
                colspan="7"
                v-if="ClassRooms.length == 0"
              >
                目前沒有課程
              </td>
            </tr>
            <template v-for="c in ClassRooms">
              <tr v-for="(l, i) in c.LessonList" :key="l.LessonID">
                <template v-if="i == 0">
                  <td :rowspan="c.Lessons.length">
                    {{ c.CreateTime | dateFormat }}
                  </td>
                  <td :rowspan="c.Lessons.length">
                    <router-link :to="'/teacher/class/edit/' + c.ClassRoomID"
                      ><img
                        class="tableInnerIcon"
                        src="/img/edit.svg" /></router-link
                    ><a href="javascript:void(0);" @click="remove(c)"
                      ><img class="tableInnerIcon" src="/img/delete.svg" /></a
                    ><router-link :to="'/teacher/class/copy/' + c.ClassRoomID"
                      ><img class="tableInnerIcon" src="/img/clone.svg"
                    /></router-link>
                  </td>
                  <td :rowspan="c.Lessons.length">{{ c.ClassRoomName }}</td>
                  <td :rowspan="c.Lessons.length">
                    {{ c.Code
                    }}<a
                      class="copyBtn"
                      href="javascript:void(0);"
                      @click="copy(c)"
                      ><img src="/img/copy.svg"
                    /></a>
                  </td>
                  <td :rowspan="c.Lessons.length">{{ c.LessonTitle }}</td>
                  <td :rowspan="c.Lessons.length" v-if="c.Status == 0">暫存</td>
                  <td :rowspan="c.Lessons.length" v-else-if="c.Status == 1">
                    {{ c.OpenStatus }}
                  </td>
                  <td width="25%">
                    <router-link :to="'/lesson/' + l.LessonID">{{
                      l.LessonName
                    }}</router-link>
                  </td>
                </template>
                <td v-else>
                  <router-link :to="'/lesson/' + l.LessonID">{{
                    l.LessonName
                  }}</router-link>
                </td>
              </tr>
            </template>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Header from "@/components/Teacher/Header.vue";
import moment from "moment";

export default {
  components: {
    Header,
  },
  data() {
    return {
      ClassRooms: [],
    };
  },
  mounted() {
    this.setBase("tSpace page");
    this.load();
  },
  methods: {
    async load() {
      var response = await this.$api.getMyClassRoom();
      var map = {};
      response.Lessons.forEach((l) => (map[l.LessonID] = l));
      response.ClassRooms.forEach((c) => {
        var list = [];
        c.Lessons.forEach((l) => list.push(map[l]));
        c.LessonList = list;
        if (moment().isBefore(c.StartTime)) {
          c.OpenStatus = "未開課";
        } else if (moment().isAfter(c.EndTime)) {
          c.OpenStatus = "已結束";
        } else {
          c.OpenStatus = "已開課";
        }
      });
      this.ClassRooms = response.ClassRooms;
    },
    copy(c) {
      window.utils.copy(c.Code);
      setTimeout(() => {
       alert("課程代碼已複製"); 
      });
    },
    async remove(c) {
      await this.$api.delClassRoom(c.ClassRoomID);
      this.load();
    },
  },
};
</script>